import { useEffect, useState } from "react";
import { editAccountValidation } from './validation'
import Axios from "../../api/Axios";
import { useNavigate, useOutletContext } from 'react-router-dom';

const axios = new Axios();

const EditAccount = () => {

  const navigate = useNavigate();
  const userData = useOutletContext()
  const [error, setError] = useState({
    firstName: false,
    lastName: false,
    email: false,
    // username: false,
    confirmPassword: false,
    message: ""
  });

  const [data, setData] = useState({
    firstName: "",
    lastName: "",
    // username: "",
    email: "",
    currentPassword: "",
    newPassword: "",
    confirmPassword: ""
  });

  // const [isUsernameTaken, setIsUsernameTaken] = useState(false)
  // const [actualUsername, setActualUsername] = useState("")
  const onChange = (value, target) => {
    setData({
      ...data,
      [target]: value,
    });
    if (error.email === true || error.confirmPassword === true || error.firstName === true || error.lastName === true) {
      setError({
        ...error,
        [target]: false
      })
    }
  };

  // const checkUsername = async () => {
  //   const options = {
  //     method: "GET",
  //   }
  //   if (data.username) {
  //     const result = await axios.callApi(`auth/checkByUsername/${data.username}`, options)
  //     if (result.data.usernameExists) {
  //       setError({ ...error, username: true })
  //       setIsUsernameTaken(true)
  //     }
  //     else {
  //       setError({ ...error, username: false })
  //       setIsUsernameTaken(false)
  //     }
  //   }
  //   else {
  //     setError({ ...error, username: false })
  //     setIsUsernameTaken(false)
  //   }

  // }

  const getUserAccountDetails = () => {
    setData({ ...data, ...userData })
    // setActualUsername(userData.username)
  }
  
  const handleSubmit = async (e) => {
    try {
      e.preventDefault()
      const valid = editAccountValidation(data)
      if (!(data.newPassword === data.confirmPassword)) {
        valid["confirmPassword"] = true
      }
      // data.username && !isUsernameTaken ? setError(valid) : setError({ ...valid, username: true })
      setError(valid)
      if (valid.firstName || valid.lastName || valid.email || valid.confirmPassword) {
        return
      }
      const options = {
        method: "POST",
        body: data
      }
      const result = await axios.callApi(`user/updateUser`, options)
      if (result.status == 200) {
        navigate("/")
      }
    }
    catch (error) {
      console.error("Something went wrong")
    }
  }

  // useEffect(() => {
  //   const typingTimer = setTimeout(() => {
  //     if (actualUsername != data.username.trim())
  //       checkUsername(data.username)
  //   }, 1000);

  //   return () => {
  //     clearTimeout(typingTimer)
  //   }
  // }, [data.username])

  useEffect(() => {
    getUserAccountDetails()
  }, [userData])

  return (
    <div className="edit-account-container container-fluid" style={{ backgroundColor: "white" }}>
      <form>
        <div className="row" style={{ marginTop: "25px" }}>
          <div className='col-lg-6 col-sm-12 form-group mb-2'>
            <label htmlFor="firstName" className='form-label'>First Name</label>
            <input type="text" id='firstName' className={`form-control ${error.firstName ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'firstName')} value={data.firstName} />
            {error.firstName && <div className='error'>
              Please enter your first name
            </div>}
          </div>
          <div className='col-lg-6 col-sm-12 form-group mb-2'>
            <label htmlFor="lastName" className='form-label'>Last Name</label>
            <input type="text" id='lastName' className={`form-control ${error.lastName ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'lastName')} value={data.lastName} />
            {error.lastName && <div className='error'>
              Please enter your last name
            </div>}
          </div>
        </div>
        {/* <div className='form-group mb-2'>
          <label htmlFor="username" className='form-label'>Username</label>
          <input type="text" id='username' className={`form-control ${error.username ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'username')} value={data.username} />
          {error.username && <div className='error'>
            {isUsernameTaken ? "Username already use" : "Please enter your username"}
          </div>}
        </div>
        <i className="text-style">This will be how your name will be displayed in the account section and in reviews</i> */}
        <div className='form-group mb-2'>
          <label htmlFor="email" className='form-label'>Email Address</label>
          <input type="email" id='email' className={`form-control ${error.email ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'email')} value={data.email} />
          {error.email && <div className='error'>
            {error.message ? "Please enter a valid email" : "Please enter your email"}
          </div>}
        </div>
        <span className="text-style" style={{ margin: "15px 0px", display: "inline-block" }}>PASSWORD CHANGE</span>
        <div className='form-group mb-2'>
          <label htmlFor="current password" className='form-label'>Current password (leave blank to leave unchanged)</label>
          <input type="password" id='current password' className={`form-control`} required onChange={e => onChange(e.target.value, 'currentPassword')} />
          {/* {error.password && <div className='error'>
            Please enter your password
          </div>} */}
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="new password" className='form-label'>New password (leave blank to leave unchanged) </label>
          <input type="password" id='new password' className={`form-control`} required onChange={e => onChange(e.target.value, 'newPassword')} />
          {/* {error.password && <div className='error'>
            Please enter your password
          </div>} */}
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="confirm password" className='form-label'>Confirm new password</label>
          <input type="password" id='confirm password' className={`form-control ${error.confirmPassword ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'confirmPassword')} />
          {error.confirmPassword && <div className='error'>
            Passwords do not match
          </div>}
        </div>
        <button className='btn btn-dark mt-5 mb-2' onClick={e => handleSubmit(e)}>SAVE CHANGES</button>
      </form>
    </div>
  )
}

export default EditAccount
