export const editAccountValidation = (data) => {
  const { firstName, lastName, email } = data;
  var errorVal = {
    firstName: false,
    lastName: false,
    email: false,
    message: ""
  }

  if (!firstName) {
    errorVal["firstName"] = true
  }
  if (!lastName) {
    errorVal["lastName"] = true
  }
  if (!email) {
    errorVal["email"] = true
  }
  if (email && !(/\S+@\S+\.\S+/.test(email))) {
    errorVal["email"] = true
    errorVal["message"] = "invalid email"
  }
  return errorVal
};

export const editAddressValidation = (data) => {
  const { firstName, lastName, email, state, streetAddress, city, postcode, phone } = data;
  var errorVal = {
    firstName: false,
    lastName: false,
    email: false,
    state: false,
    streetAddress: false,
    city: false,
    postcode: false,
    phone: false,
    message: ""
  }

  if (!firstName) {
    errorVal["firstName"] = true
  }
  if (!lastName) {
    errorVal["lastName"] = true
  }
  if (!email) {
    errorVal["email"] = true
  }
  if (email && !(/\S+@\S+\.\S+/.test(email))) {
    errorVal["email"] = true
    errorVal["message"] = "invalid email"
  }
  // if (!country) {
  //   errorVal["country"] = true
  // }
  if (!state) {
    errorVal["state"] = true
  }
  if (!streetAddress) {
    errorVal["streetAddress"] = true
  }
  if (!city) {
    errorVal["city"] = true
  }
  if (!postcode) {
    errorVal["postcode"] = true
  }
  if (!phone) {
    errorVal["phone"] = true
  }
  if (phone && phone.length != 10) {
    errorVal["phone"] = true
  }
  return errorVal
};